<script lang="ts" type="text/" src="./About.ts"></script>
<style scoped lang="scss" src="./About.scss"></style>

<template>
  <div class="container">
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Slides with custom text -->
      <b-carousel-slide>
        <h1>Hogenakkal Water Falls</h1>
        <template #img>
          <img class="img-fluid cy" src="../assets/five.jpg" alt="image slot" />
        </template>
      </b-carousel-slide>

      <!-- Slides with image only -->
      <b-carousel-slide>
        <h1>Lepakshi</h1>
        <template #img>
          <img
            class="img-fluid cy"
            src="../assets/lepakshi.jpg"
            alt="image slot"
          />
        </template>
      </b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide>
        <h1>Goa Trip</h1>
        <template #img>
          <img class="img-fluid cy" src="../assets/goa.jpg" alt="image slot" />
        </template>
      </b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide>
        <h1>Hogenakkal Water Falls</h1>
        <template #img>
          <img
            class="img-fluid cy"
            src="../assets/twowater.jpg"
            alt="image slot"
          />
        </template>
      </b-carousel-slide>

      <!-- Text slides with image -->
      <b-carousel-slide text="Nandi Hills" fluid>
        <template #img>
          <img class="img-fluid cy" src="../assets/one.jpg" alt="image slot" />
        </template>
      </b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide>
        <template #img>
          <img class="img-fluid cy" src="../assets/me.jpg" alt="image slot" />
        </template>
      </b-carousel-slide>

      <!-- Slides with img slot -->
      <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
      <b-carousel-slide fluid>
        <template #img>
          <img class="img-fluid cy" src="../assets/mee.jpg" alt="image slot" />
        </template>
      </b-carousel-slide>
    </b-carousel>

    <p style="display:none;" class="mt-4">
      Slide #: {{ slide }}<br />
      Sliding: {{ sliding }}
    </p>

    <google-map />
  </div>
</template>

<script>
import GoogleMap from "@/components/GoogleMap.vue";
export default {
  name: "App",
  components: {
    GoogleMap
  },
  data() {
    return {
      slide: 0,
      sliding: null
    };
  },
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
    },
    onSlideEnd(slide) {
      this.sliding = false;
    }
  }
};
</script>
